<template>
  <div id="my-info">
    <div class="avatar">
      <img :src="myInfo.avatar" alt="">
    </div>
    <h2 class="name">{{myInfo.name}}</h2>
    <h3 class="work">{{myInfo.work}}</h3>
    <div class="line"></div>
    <div class="language">
      <p class="title">技能</p>
      <ul class="cl">
        <li v-for="(item,key) in language">
          <img :src="item.src" alt="">
          <p>{{item.content}}</p>
        </li>
      </ul>
    </div>
    <div class="follow">
      <p class="title">点击进入</p>
      <ul class="cl">
        <li v-for="(item,key) in follow">
          <img :src="item.src" alt="" :title="item.title">
        </li>
      </ul>
    </div>
    <div class="motto">
      <p>静水深流，艰苦奋斗；</p>
      <p>修己安人，厚德载物！</p>
    </div>
    <div class="resume">
      查看简历
    </div>
  </div>
</template>

<script>
  export default {
    name: "MyInfo",
    data: function () {
      return {
        //个人头像名称职业
        myInfo: {
          avatar: require("../assets/images/avatar.png"),
          name: "Arthas 薛云皓",
          work: "web 前端"
        },
        //掌握的语言
        language: [
          {
            src: require("../assets/images/icon_HTML5.png"),
            content: "HTML"
          },
          {
            src: require("../assets/images/icon_css3.png"),
            content: "CSS"
          },
          {
            src: require("../assets/images/icon_js.png"),
            content: "javaScript"
          },
          {
            src: require("../assets/images/icon_vue.png"),
            content: "Vue"
          },
          {
            src: require("../assets/images/icon_smallApp.png"),
            content: "小程序"
          }
        ],
        //我的个人资料联系方式
        follow: [
          {
            title: "微信",
            src: require("../assets/images/icon_wechat.png")
          },
          {
            title: "微博",
            src: require("../assets/images/icon_weibo.png")
          },
          {
            title: "QQ",
            src: require("../assets/images/icon_QQ.png")
          },
          {
            title: "gitHub",
            src: require("../assets/images/icon_GitHub.png")
          }
        ]
      }

    }
  }
</script>

<style scoped lang="less">
  #my-info {
    width: 400px;
    height: 700px;
    padding: 20px 20px 0;
    background: linear-gradient(to bottom right, purple, mediumpurple);
    border-radius: 10px;
    .avatar {
      width: 100px;
      height: 100px;
      margin: 0 auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .name {
      margin-top: 10px;
      font-size: 22px;
      color: #fff;
    }
    .work {
      margin-top: 6px;
      font-size: 16px;
      color: #ccc;
    }
    .line {
      width: 100%;
      height: 2px;
      margin-top: 10px;
      background: #CF9EE0;
      border-radius: 1px;
    }
    .language {
      width: 100%;
      padding-top: 20px;
      color: #CF9EE0;
      .title {
        font-size: 20px;
      }
      ul {
        padding: 20px 0;
        background: rgba(255, 255, 255, .3);
        border-radius: 5px;
        li {
          float: left;
          margin: 0 30px 10px 30px;
          img {
            width: 50px;
            height: 50px;
            padding: 5px;
            border-radius: 50%;
            background: #fff;
          }
          p {
            font-size: 14px;
            color: #fff;
            line-height: 2;
          }
        }

      }
    }
    .follow {
      width: 100%;
      padding-top: 20px;
      color: #CF9EE0;
      .title {
        font-size: 20px;
        margin-bottom: 5px;
      }
      ul {
        height: 50px;
        padding: 10px 30px 0;
        background: rgba(255, 255, 255, .3);
        border-radius: 5px;
        li {
          float: left;
          width: 30px;
          height: 30px;
          margin: 0 5px;
          img {
            width: 100%;
            height: 100%;
            background: #fff;
            border-radius: 50%;
            cursor: pointer;
          }
        }
      }
    }
    .motto {
      margin-top: 15px;
      font-size: 16px;
      color: #CF9EE0;
    }
    .resume {
      display: block;
      width: 150px;
      height: 40px;
      margin: 20px auto;
      color: #fff;
      line-height: 40px;
      font-size: 16px;
      font-weight: bold;
      background: #8438A2;
      border-bottom: 3px solid #B55BD8;
      cursor: pointer;
      &:hover {
        background: #B55BD8;
        border-bottom: 3px solid #8438A2;
      }
    }
  }
</style>



