<template>
  <div>
    <div id="swiper" @mouseover="stop()" @mouseout="move()">
      <div class="banner">
        <transition-group tag="ul" name="image">
          <li v-for="(img,index) in imgArray" :key="index" v-show="index===mark">
            <a :href="img.link">
              <img :src="img.src" alt="">
            </a>
          </li>
        </transition-group>
      </div>
      <div class="bar">
        <span v-for="(item,index) in imgArray" :key="index" :class="{'active':index===mark}"
              @click="chang(index)"></span>
      </div>
    </div>
    <div id="project">
      <my-project></my-project>
    </div>
  </div>

</template>

<script>
  import MyProject from './MyProject'

  export default {
    name: "Swiper",
    components: {
      MyProject
    },
    data: function () {
      return {
        imgArray: [
          {
            src: require("../assets/images/banner01.jpg"),
            link: "http://chenhao.121learn.cn/"
          },
          {
            src: require("../assets/images/banner02.jpg"),
            link: "http://chenhao.121learn.cn/"
          }
        ],
        mark: "0",
        timer: null
      }
    },
    created: function () {
      //执行函数自动轮播
      this.autoPlay();
      this.play();
    },
    methods: {
      //设置自动播放规则
      autoPlay: function () {
        this.mark++;
        if (this.mark === this.imgArray.length) {
          this.mark = 0
        }
      },
      //设置定时器执行自动播放函数,间隔3秒
      play: function () {
        this.timer = setInterval(this.autoPlay, 3000)
      },
      //设置点击底部索引自动修改显示页面
      chang: function (i) {
        this.mark = i
      },
      //鼠标移入停止滚动
      stop: function () {
        clearInterval(this.timer)
      },
      //鼠标移出执行滚动
      move: function () {
        this.timer = setInterval(this.autoPlay, 3000)
      }
    }
  }
</script>

<style scoped lang="less">
  #swiper {
    width: 750px;
    height: 200px;
    position: relative;
    top: 0;
    left: 0;
    border-radius: 10px;
    overflow: hidden;
    .banner {
      ul {
        li {
          position: absolute;
          left: 0;
          top: 0;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }

      .image-enter-active {
        transform: translateX(0);
        transition: all 1.5s ease;
      }
      .image-leave-active {
        transform: translateX(-100%);
        transition: all 1.5s ease;
      }
      .image-enter {
        transform: translateX(100%);
      }
      .image-leave {
        transform: translateX(0);
      }
    }
    .bar {
      position: absolute;
      left: 0;
      width: 100%;
      bottom: 10px;
      margin: 0 auto;
      z-index: 10;
      text-align: center;
      span {
        width: 10px;
        height: 10px;
        border: 1px solid #ccc;
        border-radius: 50%;
        background: white;
        display: inline-block;
        margin-right: 10px;
        cursor: pointer;
        &.active {
          background: red;
        }
      }

    }

  }
</style>



