import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePc from '../pages/HomePc'

Vue.use(VueRouter);
let router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: '首页',
      component: HomePc
    }
  ]
});
export default router;



