<template>
  <div id="home-pc">
    <div class="wrap">
      <h1>Welcome to my website!</h1>
      <div class="body">
        <div class="my-wrap">
          <my-info></my-info>
        </div>
        <div class="swiper-wrap">
          <swiper></swiper>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  import MyInfo from '../components/MyInfo'
  import Swiper from '../components/Swiper'

  export default {
    name: "HomePc",
    components: {
      Swiper,
      MyInfo
    },
    data: function () {
      return {

      }
    }
  }
</script>

<style scoped lang="less">
  #home-pc {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: url("../assets/images/01.jpg") no-repeat;
    background-size: cover;
    .wrap {
      max-width: 1200px;
      margin: 0 auto;
      h1 {
        margin-top: 50px;
        font-size: 40px;
        color: #fff;
        font-weight: bold;
      }
      .body{
        display: flex;
        margin-top: 40px;
        flex-flow: row nowrap;
        justify-content: space-between;
        .my-wrap{
          width: 400px;
        }
      }
      .swiper-wrap{
        width: 750px;
      }
    }

  }
</style>



